<template>
  <div class="animated fadeIn">
    <b-card header-tag="header">
      <div slot="header">
        <b-row>
          <b-col>
            <i class="icon-drop"></i> NhaMayGiaLap
          </b-col>
          <b-col class="text-right">
            <b-dropdown text="View Source" class="m-0 p-0">
              <b-dropdown-item class="p-0" v-for="(x,index) in [
                `/src/components/ComTest2/${name}`,
                `/src/store/modules/NhaMayGiaLap.js`,
                '/src/store/MutationType2.js',
                '/src/store/index.js',
              ]" :key="index">
                  <com-vs2 :file-path-link=" x " ></com-vs2>
              </b-dropdown-item>
            </b-dropdown>
          </b-col>
        </b-row>
      </div>
      <b-card-body>


            <button @click="on_add()">add</button>
            <b-table striped hover small v-if=" sel.hasOwnProperty('id')==false "
            :items="items" 
            :fields="fields"
            >
            <template slot="cmd" slot-scope="row">
              <button @click="on_edit(row)">
                <i class="fa fa-pencil" aria-hidden="true"></i>
              </button>
              <button @click="on_del(row)">
                <i class="fa fa-trash" aria-hidden="true"></i>
              </button>
            </template>

            </b-table>
            <div v-if=" sel.hasOwnProperty('id')==true " class="p-5">
              <div id="frm">
                <h3 v-if="sel.id==0">Thêm Nông Trại</h3>
                <h3 v-if="sel.id!=0">Sửa Nông Trại</h3>
              <b-row>
                <b-col cols="4">id</b-col>
                <b-col>{{sel.id}}</b-col>
              </b-row>
              <b-row>
                <b-col cols="4">Tên</b-col>
                <b-col><input type="text" v-model="sel.ten" class="my-crt" /></b-col>
              </b-row>
              <b-row>
                <b-col cols="4">Địa chỉ</b-col>
                <b-col><input type="text" v-model="sel.dia_chi" class="my-crt" /></b-col>
              </b-row>
              <b-row>
                <b-col cols="4">Chứng Nhận</b-col>
                <b-col><input type="text" v-model="sel.chung_nhan" class="my-crt" /></b-col>
              </b-row>
              <b-row>
                <b-col cols="4"></b-col>
                <b-col>
                  <button v-if="sel.id!=0" @click="on_update()">update</button>
                  <button v-if="sel.id==0" @click="on_do_add()">add</button>
                  <button @click="on_cancel()">cancel</button>
                </b-col>
              </b-row>
              </div>

            </div>





      </b-card-body>
    </b-card>

  </div>
</template>

<script>
//import ColorTheme from './ColorTheme'
import Repository2 from '@/store/Repository2';
import types from '@/store/MutationType2';
import moment, { months } from 'moment';

export default {
  name: 'NhaMayGiaLap.vue',
  components: { 
    //ColorTheme 
  },
  computed:{
    items(){
      return this.$store.getters[types.NHA_MAY_GIA_LAP_GET_DS];
    }
  },

  data(){
    return{
      name:`${this.$options.name}`,
      sel:{},
      fields:[
        'id',
        'ten',
        'dia_chi',
        'chung_nhan',
        'cmd'
      ]

    }
  },

  created(){
    this.$store.dispatch(types.NHA_MAY_GIA_LAP_LDS).then(res=>{
      //alert('load done')
    })
    //alert(JSON.stringify(ds));
  },
  methods:{
    on_add(){
        this.sel={
            id:0,
            ten:'Nhà Máy XYZ',
            dia_chi:'Dia chi xyz',
            chung_nhan:'Chung Nhan XYZ',
        }
    },
    on_do_add(){
      let r=Object.assign({},this.sel);
      this.$store.dispatch(types.NHA_MAY_GIA_LAP_ADD,r)//.NONG_TRAI_ADD,r)
      .then(res=>{
        this.on_cancel();
      })
    },

    on_del(row) {
      this.$store.dispatch(types.NHA_MAY_GIA_LAP_DEL,row.item.id);//.NONG_TRAI_DEL,row.item.id);
    },
    on_edit(row) {
      this.sel = JSON.parse(JSON.stringify(row.item));
    },
    on_update(){
        let r=Object.assign({},this.sel);
        this.$store.dispatch(types.NHA_MAY_GIA_LAP_UPDATE,r)//.NONG_TRAI_UPDATE,r)
        .then(res=>{
          this.on_cancel();
        })
    },
    on_cancel(){
        this.sel={}
    }

  },



}
</script>
